interface BallProps{
    right?:string
    top?:string
    left?:string
    bottom?:string
    animNumber:number
    transitionDuration:string
}

const fly = keyframes`
  0% { transform: translate(0, 0%); }
  25% { transform: translate(0, 20%); }
  50% { transform: translate(0, 0%); }
  75% {  transform: translate(0, -20%); }
  100% { transform: translate(0, 0%); }


`;

const fly2 = keyframes`
  0% { transform: translate(0, 0%); }
  25% { transform: translate(-20px, -20%); }
  50% { transform: translate(10px, 0%); }
  75% {  transform: translate(-10px, -20%); }
  100% { transform: translate(0, 0%); }


`;

const fly3 = keyframes`
  0% { transform: translate(0, 0%); }
  25% { transform: translate(-20px, 20%); }
  50% { transform: translate(-10px, 0%); }
  75% {  transform: translate(10px, -20%); }
  100% { transform: translate(0, 0%); }


`;

export const BallWrapper = styled.div`
display:none;
 @media (min-width:1280px){
     display:block;
 }
`
export const Ball = styled.div<BallProps>`
    background: radial-gradient(75.23% 71.67% at 34.58% 3.33%, rgba(255, 255, 255, 0.33) 0%, rgba(255, 255, 255, 0) 100%);
border: 1px solid rgba(255, 255, 255, 0.08);
height:240px;
width:240px;
border-radius:50%;
display:flex;
align-items:center;
justify-content:center;
position:absolute;
left: ${({left}) => left && left};
right: ${({right}) => right && right};
top: ${({top}) => top && top};
bottom: ${({bottom}) => bottom && bottom};
font-size: 18px;
line-height: 110%;
text-align: center;
color: #FFFFFF;
z-index:10;
padding:25px;
animation: ${({animNumber}) => animNumber === 1 ? fly : animNumber === 2 ? fly2 : fly3} ${({transitionDuration}) => transitionDuration} linear infinite;

`

