import React,{FC,useState} from 'react'
import {Wrapper,ContentWrapper,GreatlyText,SnowWrapper,MobileBallWrapper,MobileBall, Header,HeaderLeft,SofiaLogo,BallWrapper,CameraBlock,CameraIconWrapper,CameraDot,CameraTitle, Visa,Separator,LanguageSwitcher,Option,Logo,LogoTitle,Title, Subtitle, Text, ActionButton,HowItWorks,HowItWorksTitle,Item,ItemCircle,CircleBorder,ItemTitle,ItemSubtitle,ItemList,Copyright, Ball} from './styled'
import LogoImg from '../../img/logo.png'
import SofiaImg from '../../img/sofia.png'
import { CameraIcon } from '../../img/CameraIcon'
import {Form} from './Form'
import FadeIn from 'react-fade-in';


export const Home:FC =  () => {

    const [currentLangOption,setCurrentLangOption] = useState<string>('ua') 
    const [formIsOpen,toggleForm] = useState(false)
    const snowCount = Array.from(Array(100).keys())

    
    return(
            <Wrapper>
                      <SnowWrapper>  {snowCount.map((item,index) => <div key={index} className="snow" /> )}</SnowWrapper>

                {!formIsOpen  ? 
                <>

                    <ContentWrapper>
                    <Header>
                        <HeaderLeft>
                            <SofiaLogo src={SofiaImg}/>
                        <Visa/>
                        <CameraBlock>
                            <CameraIconWrapper><CameraIcon/></CameraIconWrapper>
                            <CameraDot/>
                            <CameraTitle>online</CameraTitle>
                        </CameraBlock>
                        </HeaderLeft>
    
                        <LanguageSwitcher>
                            <Option onClick ={() => setCurrentLangOption('ua')} active={currentLangOption === 'ua'}>Ua</Option> <Separator>/</Separator> <Option  onClick ={() => setCurrentLangOption('eng')} active={currentLangOption === 'eng'}>Eng</Option>
                        </LanguageSwitcher>
                
                    </Header>
                    <FadeIn transitionDuration={2000}>
                    <Logo src={LogoImg}/>
                    <LogoTitle>
                        Інтерактивне лазерне шоу
                        </LogoTitle>
                    </FadeIn>
                    <FadeIn delay={200} transitionDuration={2000}>
                    <Title>
                        Вітання Звідусіль
                        </Title>
                        </FadeIn>
                        <FadeIn delay={300} transitionDuration={2000}>

                        <Subtitle>
                        Ваше привітання на головній ялинці України
                        </Subtitle>
                        </FadeIn>
                        <FadeIn delay={600} transitionDuration={3000}>

                        <Text>
                        З 19 грудня на Софіївській площі у Києві почнеться унікальне лазерне шоу, автором якого можете стати ви!
                        </Text>

                        <Text>

Залиште вітання на цьому сайті, підпишіть його. І воно з'явиться у Новорічні свята на Дзвіниці Софійського собору. Ви зможете спостерігати за цим моментом онлайн на сайті і отримати фото на згадку на свій імеіл.

                        </Text>
                        <Text>
                        Проєкт створено Національним заповідником «Софія Київська» у партнерстві з Visa, щоб незважаючи на обставини, труднощі і складнощі, ви могли передати теплі слова тим, кого ви любите, де б ви зараз не були, з будь-якої точки планети. Ви зможете привітайте зі святами один одного чи всю країну, конкретну людину чи цілу родину, того, хто поруч і того, хто через карантин залишився дуже далеко. 

                        </Text>
                        </FadeIn>
                        <FadeIn delay={1000} transitionDuration={3000}>

                        <GreatlyText>З наступаючими святами!</GreatlyText>
                        </FadeIn>
                        </ContentWrapper>
                        <ActionButton onClick={() => toggleForm(true)}>Надіслати привітання</ActionButton>
                        <BallWrapper>
                        <FadeIn transitionDuration={1000}>

                            <Ball transitionDuration="5s" animNumber={1} top='510px' left="60px">   7139-кілометрове вітання рідному Ужгороду! Інна, Нью-Йорк</Ball>
                            </FadeIn>
                            <FadeIn  transitionDuration={1000}>

                            <Ball transitionDuration="8s" animNumber={2} top='380px' right="60px">   З новим роком, мій маленький Андрійко. Твоя мама! Наталія, Кременчук </Ball>
                            </FadeIn>
                            <FadeIn transitionDuration={1000}>

                            <Ball transitionDuration="4s" animNumber={3} top='823px' right="215px">  Бабусю, дякую тобі за те, що залишаєшся вдома! Софія, Ірпінь </Ball>
                            </FadeIn>
                            <FadeIn  transitionDuration={1000}>

                            <Ball transitionDuration="8s" animNumber={1} top='943px' left="229px">  Оксано,  вийдеш за мене? Петро, Кривий Ріг </Ball>
                            </FadeIn>
                            <FadeIn  transitionDuration={1000}>

                            <Ball transitionDuration="7s" animNumber={2} top='1111px' right="76px">  Україно, нехай твій 2021-й буде сповнений перемог! Антон, Бердичів </Ball>
                            </FadeIn>

                        </BallWrapper>
                        <MobileBallWrapper>
                            <MobileBall transitionDuration="5s" animNumber={1} top='510px' left="60px">   7139-кілометрове вітання рідному Ужгороду! Інна, Нью-Йорк</MobileBall>
                            <MobileBall transitionDuration="8s" animNumber={2} top='380px' right="60px">   З новим роком, мій маленький Андрійко. Твоя мама! Наталія, Кременчук </MobileBall>
                        </MobileBallWrapper>
                        <HowItWorks>
                        <FadeIn delay={200} transitionDuration={2000}>

                            <HowItWorksTitle>Як це працює:</HowItWorksTitle>
                            </FadeIn>
                            <ItemList>
                            <FadeIn delay={600} transitionDuration={2000}>

                            <Item>
                                <ItemCircle><CircleBorder steps={1}/>01</ItemCircle>
                                <ItemTitle>Заявка</ItemTitle>
                                <ItemSubtitle>Заповніть форму з вашим вітанням.</ItemSubtitle>
                            </Item>
                            </FadeIn>
                            <FadeIn delay={1200} transitionDuration={2000}>

                            <Item>
                                <ItemCircle><CircleBorder steps={2}/>02</ItemCircle>
                                <ItemTitle>Підтвердження</ItemTitle>
                                <ItemSubtitle>Після перевірки вітання* чекайте на лист з точним часом його трансляції.</ItemSubtitle>
                            </Item>
                            </FadeIn>
                            <FadeIn delay={1800} transitionDuration={2000}>

                            <Item>
                                <ItemCircle><CircleBorder steps={3}/>03</ItemCircle>
                                <ItemTitle>Готово</ItemTitle>
                                <ItemSubtitle>У визначений час лазерне шоу протранслює ваше вітання на головній новорічній площі України, а ви отримаєте його фото на пошту.</ItemSubtitle>
                            </Item>
                            </FadeIn>
                            </ItemList>
                            <FadeIn delay={2000} transitionDuration={2000}>

                            <ActionButton onClick={() => toggleForm(true)}>Надіслати привітання</ActionButton>
                            </FadeIn>
    
                        </HowItWorks>
                        <Copyright>©Copyright 1996-2020. All Rights Reserved.</Copyright>
                    </>
            :
            <ContentWrapper>
                             <Header>
                        <HeaderLeft>
                            <SofiaLogo src={SofiaImg}/>
                        <Visa/>
                        <CameraBlock>
                            <CameraIconWrapper><CameraIcon/></CameraIconWrapper>
                            <CameraDot/>
                            <CameraTitle>online</CameraTitle>
                        </CameraBlock>
                        </HeaderLeft>
    
                        <LanguageSwitcher>
                            <Option onClick ={() => setCurrentLangOption('ua')} active={currentLangOption === 'ua'}>Ua</Option> <Separator>/</Separator> <Option  onClick ={() => setCurrentLangOption('eng')} active={currentLangOption === 'eng'}>Eng</Option>
                        </LanguageSwitcher>
                
                    </Header>
                    <Form setIsForm={() => toggleForm(false)}/>

            </ContentWrapper>
            }
            
            </Wrapper>
    )
}